<template>
  <div class="aoodsShelvesEfficacy">
    <AppHeader>
      <p slot="center">货架效能</p>
    </AppHeader>

    <div class="filters">
      <div class="date-filters">
        <input type="text" @click.stop="openByDialog" :value="calendar.display" readonly />
      </div>
      <transition name="fade">
        <div class="calendar-dialog" v-if="calendar.show">
          <div class="calendar-dialog-mask" @click="closeByDialog"></div>
          <div class="calendar-dialog-body">
            <calendar
              :range="calendar.range"
              :zero="calendar.zero"
              :lunar="calendar.lunar"
              :value="calendar.value"
              @select="calendar.select"
            ></calendar>
          </div>
        </div>
      </transition>
    </div>

    <div class="contrast">
      <van-tabs v-model="salesActive" @click="salesTabClick">
        <van-tab title="销售额"></van-tab>
        <van-tab title="毛利额"></van-tab>
      </van-tabs>
    </div>

    <div class="tips">
      <p>
        小贴士：月销售额低于2000，
        <span style="color: red">红色</span>显示；月销售额2000-5000，
        <span style="color: #ff0">黄色</span> 显示；月销售额高于5000，
        <span style="color: green">绿色</span> 显示。
      </p>
    </div>

    <div class="detailed-table" v-if="hasData">
      <div class="table-content">
        <table>
          <thead>
            <tr>
              <th>部门</th>
              <th>货架编号</th>
              <th>货架效能</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(items, index) in resultData" :key="index">
              <td>{{ items.deptNo }}</td>
              <td>{{ items.rcode }}</td>
              <td>
                <div>
                  <van-progress :percentage="50" :show-pivot="false" stroke-width="8" />
                  <p>{{ items.salesVolume }}</p>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Tab, Tabs, Calendar, Field, Progress } from "vant";

Vue.use(Tab)
  .use(Tabs)
  .use(Calendar)
  .use(Progress)
  .use(Field);

import AppHeader from "@/components/AppHeader.vue";
import calendar from "@/components/calendar/calendar.vue";

export default {
  name: "AoodsShelvesEfficacy",
  components: {
    AppHeader,
    calendar
  },

  data() {
    return {
      active: 0,
      salesActive: 0,
      screenWidth: document.body.clientWidth, // 屏幕尺寸
      offsetTop: 90 / (750 / document.body.clientWidth),

      calendar: {
        display: "2020/02/16 ~ 2020/03/16", //输入框站位日期
        show: false, //是否显示
        range: true,
        zero: true,
        value: [
          [2018, 2, 16],
          [2019, 2, 16]
        ], //默认选中日期
        lunar: true, //显示农历
        select: (begin, end) => {
          console.log(begin, end);
          this.calendar_startDate = begin.join("-");
          this.calendar_endDate = end.join("-");
          this.calendar.show = false;
          this.calendar.value = [begin, end];
          this.calendar.display = begin.join("/") + " ~ " + end.join("/");
          this.$nextTick(() => {
            this.get_data();
          });
        }
      },
      calendar_startDate: "",
      calendar_endDate: "",
      getStoreInfo: "",
      sku_myChart: null,
      resultData: [],
      hasData: false
    };
  },
  created() {
    this.get_defaultData();
    this.get_data();
  },
  mounted() {},
  methods: {
    salesTabClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {});
      } else if (name == 1) {
        this.$nextTick(() => {});
      }
    },
    openByDialog() {
      this.calendar.show = true;
    },
    closeByDialog() {
      this.calendar.show = false;
    },
    get_defaultData() {
      this.getStoreInfo = this.$store.getters.getStoreInfo; //获取当前选择的门店信息
      console.log(this.$store.state.defaultDate);
      const time1 = this.formatDate.pastDate(
        this.$store.state.defaultDate + 1,
        "-"
      );
      const time2 = this.formatDate.pastDate(2, "-");
      this.calendar_startDate = time1; //日历默认开始时间
      this.calendar_endDate = time2; //日历默认结束时间
      this.calendar.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");
    },
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.getStoreInfo = this.$store.getters.getStoreInfo;
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/shelf/shelfEfficiency", params)
        .then(res => {
          this.toast.clear();
          console.log(res);
          const resultData = res.data.data;
          if (resultData.length == false) {
            this.hasData = false;
          } else {
            this.hasData = true;
            this.resultData = resultData;
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    }
  }
};
</script>

